import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import OccupantDashboard from './occupants/OccupantDashboard';

const BASE = process.env.PUBLIC_URL || '';
const DEFAULT_SAISON = process.env.REACT_APP_UXCO_SAISON || 'ete_2025';

export default function DashboardWrapper() {
  const { residence } = useParams();
  const navigate = useNavigate();
  const [isVerifying, setIsVerifying] = useState(true);
  const [residenceExists, setResidenceExists] = useState(false);

  useEffect(() => {
    const verifierResidence = async () => {
      if (!residence) {
        // Pas de résidence dans l'URL, on reste sur la page d'accueil
        setIsVerifying(false);
        return;
      }

      try {
        // Nettoyer le nom de la résidence (remplacer espaces par underscores, minuscules)
        const residenceClean = residence.toLowerCase().replace(/ /g, '_');
        
        // Construire le nom du fichier principal
        const filename = `${residenceClean}_${DEFAULT_SAISON}.json`;
        const url = `${BASE}/data/${filename}`;
        
        console.log('[DashboardWrapper] Vérification de la résidence:', residenceClean);
        console.log('[DashboardWrapper] Tentative de chargement:', url);
        
        // Tenter de charger le fichier JSON principal
        const response = await fetch(url);
        
        if (!response.ok) {
          throw new Error(`Fichier ${filename} introuvable (HTTP ${response.status})`);
        }
        
        // Vérifier que c'est bien du JSON
        const contentType = response.headers.get('content-type');
        if (!contentType || !contentType.includes('application/json')) {
          throw new Error(`Le fichier ${filename} n'est pas au format JSON`);
        }
        
        // Tenter de parser le JSON
        const data = await response.json();
        
        if (!data || typeof data !== 'object') {
          throw new Error(`Le fichier ${filename} ne contient pas de données valides`);
        }
        
        console.log('[DashboardWrapper] ✓ Résidence trouvée et valide:', residenceClean);
        setResidenceExists(true);
        
      } catch (error) {
        console.error('[DashboardWrapper] ✗ Résidence introuvable:', error.message);
        // Rediriger vers la page d'erreur
        navigate(`/residence-introuvable/${residence}`, { replace: true });
      } finally {
        setIsVerifying(false);
      }
    };

    verifierResidence();
  }, [residence, navigate]);

  // Pendant la vérification, afficher un loader
  if (isVerifying) {
    return (
      <div 
        style={{
          minHeight: '100vh',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          background: 'linear-gradient(135deg, #F0F4FF 0%, #E8EFFF 50%, #D6E4FF 100%)',
          fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif'
        }}
      >
        <div style={{ textAlign: 'center' }}>
          <div 
            style={{
              width: '60px',
              height: '60px',
              border: '4px solid rgba(27, 71, 247, 0.2)',
              borderTop: '4px solid #1b47f7',
              borderRadius: '50%',
              animation: 'spin 1s linear infinite',
              margin: '0 auto 20px'
            }}
          />
          <p style={{ fontSize: '18px', color: '#4A5568', fontWeight: '500' }}>
            Vérification de la résidence...
          </p>
        </div>
        <style>
          {`
            @keyframes spin {
              0% { transform: rotate(0deg); }
              100% { transform: rotate(360deg); }
            }
          `}
        </style>
      </div>
    );
  }

  // Si la résidence existe, charger le dashboard
  if (residenceExists) {
    return <OccupantDashboard residence={residence} saison={DEFAULT_SAISON} />;
  }

  // Sinon, ne rien afficher (la navigation vers la page d'erreur est déjà en cours)
  return null;
}
