import React from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import './App.css';
import PageAccueilUXCO from './components/PageAccueilUXCO';
import PageResidenceIntrouvable from './components/PageResidenceIntrouvable';
import DashboardWrapper from './components/DashboardWrapper';

export default function App() {
  return (
    <Router basename="/analyse/UXCO">
      <Routes>
        {/* Route racine : page d'accueil */}
        <Route path="/" element={<PageAccueilUXCO />} />
        
        {/* Route d'erreur : résidence introuvable */}
        <Route path="/residence-introuvable/:residence" element={<PageResidenceIntrouvable />} />
        
        {/* Route dynamique : tentative de chargement d'une résidence */}
        <Route path="/:residence" element={<DashboardWrapper />} />
      </Routes>
    </Router>
  );
}
